export default {
  name: "AirdropAddressCard",
  props: {
    showAirdropAddress: {
      type: Boolean,
      default: false
    },
    LotteryList: {
      type: Array,
      default: () => []
    }
  },
  data() {
    return {
      MintNum: 0,
      visables: this.showAirdropAddress,
    }
  },
  computed: {
    showList() {
      const list = []
      let dr = []
      for (let i = 0; i < this.LotteryList.length; i++) {  
        if (dr.length < 3) {
          dr.push(this.LotteryList[i])
        }
        if (i % 3 === 2) {
          list.push(dr)
          dr = []
        } 
      } 
      return list
    }
  },
  watch: {
    showAirdropAddress(n) {
      this.visables = n;
    },
  },
  methods: {
    close() {
      this.$emit("closeAirdropAddressCard", false);
    },
    touchmove(event) {  
    }, 
  },
  async mounted() {
   
  }
}

